@import '../_utils.css';

$imgSize: 6rem;

.container {
  display: flex;
}

.card {
  composes: bezier-transform;

  align-items: center;
  background-color: rgba(240, 240, 240, 0.75);
  display: flex;
  flex-direction: column;
  margin: 1rem;
  padding: 1rem;
  width: 10rem;

  &:hover,
  &:focus {
    cursor: pointer;
    transform: scale(1.05);
  }
}

.dashed {
  border: 1px dashed black;
}

.icon {
  margin-bottom: 0.75rem;
  overflow: hidden;

  img {
    border-radius: 50%;
    height: $imgSize;
    width: $imgSize;
  }
}

.name {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  width: 100%;
}



