@import '../_colors.css';

.body {
  color: $text-color;
  background: $background-color;
  padding: 3em 0 6em;
  text-align: center;
}

.apps {
  background: white;
  border-radius: 0.5em;
  margin: 0 auto;
  max-width: 980px;
  padding: 1.5em;
  text-align: left;
}

.footer {
  font-size: 0.75em;
  margin: 1em;
  padding: 1.5em;
  text-align: center;
}

.header {
  background: $blue;
  color: white;
  left: 0;
  padding: 1em;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 25;
}

.loading {
  text-align: center;
  padding-top: 5em;
  font-size: 2em;
  color: $loading-color;
}



